export default {
	props:{
		placeholder:{
			default: '',
			type: String
		}	
	},
	data () {
		return {
			clear: false,
			value: '',
		}
	},
	methods: {
		onInput () {
			let value = this.value;
			value != '' ? this.clear = true : this.clear = false;
			this.$emit('searchValue',value)
		},
		clearValue () {
			this.value ='';
			this.clear = false;
		}
	},
	mounted: function() {
  }
}



