export default {
    props:{
        MStyle:{
            type: Boolean,
            default: false
        }
    },
	data(){
	    return{
	        navList:[
				{
					name:'首页',
					url:'/goodsIndex'
				},
				{
					name:'全部推荐',
					url:'/allGoods'
				},
				{
					name:'常用推荐',
					url:'/commonRecommend'
				},
				{
					name:'历史推荐 ',
					url:'/historyRecommend'
				}
			],
	    }
	},
	created(){
	},
	methods: {
		navClick(i,idx){
			this.$store.dispatch('pushnavNum',idx)
			this.$store.dispatch('pushsearchText',null)
			this.$router.push(``+i.url)
		}
	}
}



