export default {
    props:{
        navNum:{
            type: Number,
			default: 0
        }
    },
	data(){
	    return{
	        search:this.$store.state.searchText,
			user: this.$store.state.userInfo,
			AreaName:this.$store.state.inspctArea?this.$store.state.inspctArea.AreaName:'全部地区',
			AreaId:this.$store.state.inspctArea?this.$store.state.inspctArea.AreaId:0,
	    }   
	},
	created(){
		if(!this.$store.state.inspctArea){
			this.getHospitalByDrId()
		}
	},
	activated(){
		this.search = this.$store.state.searchText;
	},
	methods: {
		getHospitalByDrId(){
			this.$bridge.callhandler('pullArea','',(data) => {
				var py = /[A-Za-z]/i; 
				if(data && !py.test(data)){
					this.AreaName = data
					this.province = {
						AreaId:0,
						AreaName:data
					}
				}else{
					this.AreaId = 0
					this.AreaName = ''
					this.province = {
						AreaId:0,
						AreaName:''
					}
				}
				this.$emit('childFn',this.province)
				this.$store.dispatch('pushinspctArea',this.province)
			    responseCallback('接收到方法')
				console.log(11)
			})
		},
		goArea(){
			this.$router.push(`/GoodsArea`)
		},
		searchFor(){
			this.$store.dispatch('pushsearchText',this.search)
			this.$store.dispatch('pushIsFresh', false)
			this.$emit('searchforList')
		},
	}
}



