export default {
    props:{
        icon: {
            type: Boolean,
            default: false
        },
        tip: {
            type: Boolean,
            default: false
        },
        toIM: {
            type: Boolean,
            default: false
        },
        tips: {
            type: Boolean,
            default: false
        },
		checkId:{
			type: Number,
			default: 100
		},
		checkBack:{
			type: Number,
			default: 1
        },
        toSetAppointment: {
            type: Boolean,
            default: false
        },
    },
    data(){
        return{
            showConfirm: false,
            showConfirm2: false,
            showConfirm3: false,
            flag: false,
            flag2: false,
            zIndex:true,
        }
    },
    methods: {
        onCancel(){
            this.$emit('forCancel')
		},
		onConfirm(){
            this.$emit('forConfirm')
		},
        confirm(){
            this.flag = true
            this.flag2 = true
            this.$store.dispatch('pushpullFlag', 0)
            this.back()
        },
		confirm2(){
		    this.flag = true
		    this.flag2 = true
		    this.$store.dispatch('pushpullFlag', 0)
		    const data = '123'
		    this.$bridge.callhandler('Back', data)
		},
        toSave(){
            this.flag2 = true
            this.$emit('forSave')
        },
        back(){
			this.$bridge.callhandler('printdata','默认状态'+this.checkId)
			this.$store.dispatch('pushIsFresh', false)
			this.$store.dispatch('pushpullFlag', 1)
            if(this.tip && !this.flag){
               return this.showConfirm = true
            }
            if(this.tips && !this.flag2){
               return this.showConfirm2 = true
            }
			if(this.toSetAppointment){
                return this.showConfirm3 = true
            }
			if(this.checkId == 2){
				const data = '123'
				this.$bridge.callhandler('Back', data)
				return
            }
			if(this.checkBack == 2){
				const data = '123'
				this.$bridge.callhandler('Back', data)
				return
			}
			if(this.checkId == 1){
				this.$store.dispatch('pushpreList', [])
				this.$store.dispatch('pushgoodList', null)
				this.$store.dispatch('pushscrollPo', null)
				this.$store.dispatch('pushallSpa',null)
				this.$store.dispatch('pushinspctArea',null)
				this.$store.dispatch('pushprevUrl',this.$route.path)
				this.$router.back()
				return
			}
			if(this.checkId == 3){
				this.$store.dispatch('pushnavNum',0)
				this.$router.push(`/goodsIndex`)
				return
            }
           	if(this.toIM || this.$store.state.backIM){
                if(this.$store.state.backIM){
                    this.$store.dispatch('pushBackIM', 0);
                }
                const data = '123'
                this.$bridge.callhandler('BackToIM', data)
            }
            else{
                this.$store.dispatch('pushcMed', null)
                const now = location.href.split('?')[0]
                const start = this.$store.state.startUrl
                if(now === start){
                    const data = '123'
                    this.$bridge.callhandler('Back', data)
                }
                else{
                    this.$router.back()
					this.$store.dispatch('pushprevUrl',this.$route.path)
                }
            }
        }
    }
}



